/* định nghĩa trung */

*{
  font-family: sans-serif;
  scroll-behavior: smooth;
 
 
  

}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button:focus
  {
    outline: none;
  }
input:focus
  {
    border: 1px solid red;
  }
/* End định nghĩa trung */




/* header */


.nav{
  height: 62px;
  width: 100%;
 
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 1px rgba(0, 0,0, 0.05);
  position: fixed;
  z-index: 10;
  top: 0;
  background-color: white;
  
}

.nav-logo
  {
    font-size: 40px;
    color: rgb(255, 51, 0);
    margin-top: 10px;
    margin-left: 30px;

  }

.nav-list ul
  {
    display: flex;
    margin-top: 20px;
    

  }

.nav-list ul li
  {
    margin:0 15px;
    font-size: 19px;
    
  }
.nav-list ul li a
  {
    color: black;
  }

.nav-login
  {
    text-align: center;
    margin-top: 20px;
    margin-right: 30px;
  }
.nav-login p
  {
     color: #666;
  }
.nav-login p:first-child
  {
    margin-left: 10px;
  }
.nav-bars
  {
    font-size: 25px;
    top: 15px;
    right: 20px;
    display: none;
    position: fixed;
  }
.nav-truottrai
  {
    width: 250px;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(240, 229, 229);
    z-index: 1;
    opacity: 0;
    transform: translateX(100%);
    transition: all 1s;
  }
.hienten{
  display: block;
}

.anten{
  display: none;
}
.nav input
  {
    display: none
  }
.nav input:checked ~ .nav-truottrai
  {
    display: block;
    transform: translateX(0);
    opacity: 1;
  }
.nav input:checked ~ .nav-baophu
  {
    display: block;
    opacity: 1;
  }
.nav-truottrai label
  {
    font-size: 30px;
   top: 10px;
  left: 10px;
    position: relative;
  }
.nav-truottrai ul
  {
    z-index: 2;
  }

.nav-baophu
  {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0,0, 0.3);
    display: none;
  }
.nav-truottrai ul li
  {
    margin-top:33px;
    font-size: 20px;
    margin-left: 15px;
   
  
  }
.nav-truottrai ul li i{
  margin-right: 20px;
  color: violet;
}
@media (max-width:740px)
  {

    .nav-list{
      display: none;
    }

    .nav-login
      {
        display: none;
      }
    .nav-bars
      {
        display: block;
      }
    
  }
 




/* END header */


/* slider */




.section
{
  width: 100%;
 
  
  
}
.section-center
{
  width: 100%;
  overflow: hidden;
}
article {
  position: absolute;
 top: 80px;
  left: 0;
  width: 100%;
  height: 100%;
 opacity: 0;
  transition: all 1s;
}
.person-img
  {
    width: 100%;
    height: auto;
  }
article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
  opacity: 0.2;
 
}
article.nextSlide {
  transform: translateX(100%);
  
 
}
.prev
  {
    position: absolute;
    z-index: 2;
    margin-top: 26%;
   left: 3.9%;
    font-size: 70px;
    color: rgb(192, 184, 170);
    font-weight: bold;
  }
.prev:hover {
  color: red;
}
.next
  {
    position: absolute;
    z-index: 2;
    margin-top:26%;
    right: 3.9%;
    font-size: 70px;
    color: rgb(192, 184, 170);
    font-weight: bold;
  }
.next:hover {
    color: red;
  }

.input-radio 
  {
    position: absolute;
    margin-top: 41%;
    z-index: 1;
    margin-left: 43%;
  }
.input-radio input
  {
    margin-left: 15px;
  }




  @media (max-width:759px)
  {.next{
    margin-top: 30%;
  }
  .prev
    {
      margin-top: 30%;
    }
  .input-radio
    {
     margin-top: 50%;
     margin-left: 39%;
    }

  }


/* END slider */



